'use strict';

/**
* Evaluate the moment-generating function (MGF) for a binomial distribution.
*
* @module @stdlib/stats-base-dists-binomial-mgf
*
* @example
* import mgf from '@stdlib/stats-base-dists-binomial-mgf' ;
*
* var y = mgf( 0.5, 20, 0.2 );
* // returns ~11.471
*
* y = mgf( 5.0, 20, 0.2 );
* // returns ~4.798e29
*
* y = mgf( 0.9, 10, 0.4 )
* // returns ~99.338
*
* @example
* import { factory as factory } from '@stdlib/stats-base-dists-binomial-mgf' ;
*
* var mgf = factory( 10, 0.5 );
*
* var y = mgf( 0.3 );
* // returns ~5.013
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property' ;
import main from './main.js' ;
import factory from './factory.js' ;


// MAIN //

setReadOnly( main, 'factory', factory );


// EXPORTS //

export default main;
